import React, { useMemo, useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route, Link, useParams, Navigate, useNavigate } from "react-router-dom";
import { Container, Typography, Box, AppBar, Toolbar, Button, Grid, IconButton, Tooltip, CssBaseline } from "@mui/material";
import { createTheme, ThemeProvider } from "@mui/material/styles";
import { useTranslation } from "react-i18next";
import { motion, useScroll, useTransform } from "framer-motion";
