const dronesList = [
  { id: "drone1", name: "MOONSA FED1" },
  { id: "drone2", name: "MOONSA XF-47" },
  { id: "drone3", name: "MOONSA XF-44 Plus" },
  { id: "drone4", name: "MOONSA XF44" },
  { id: "drone5", name: "MOONSA Aryo FF One" },
  { id: "drone6", name: "MOONSA F48 Plus" },
];

const sixthGenDrones = [
  { id: "sg-drone1", name: "MOONSA SGX-900" },
  { id: "sg-drone2", name: "MOONSA SGX-920" },
  { id: "sg-drone3", name: "MOONSA SG Ultimate" },
  { id: "sg-drone4", name: "MOONSA Hyper x-12 (new)" },
  { id: "sg-drone5", name: "MOONSA Hyper X-9 (new)" },
];

const peacefulDrones = [
  { id: "pd-drone1", name: "MOONSA Peace One" },
  { id: "pd-drone2", name: "MOONSA Harmony X" },
  { id: "pd-drone3", name: "MOONSA Eco Flyer" },
  { id: "pd-drone4", name: "MOONSA Serenity Plus (new)" },
];

// بارگذاری تصاویر
const importAllImages = (r) => {
  const images = {};
  r.keys().forEach((key) => {
    const parts = key.split("/");
    const id = parts[1];
    if (!images[id]) images[id] = [];
    images[id].push(r(key));
  });
  return images;
};
const droneImages = importAllImages(require.context("./assets/images", true, /\.jpg$/));

// ===============================
// کامپوننت‌ها
// ===============================
const MotionBox = motion(Box);

const fadeUp = {
  initial: { opacity: 0, y: 24 },
  whileInView: { opacity: 1, y: 0 },
  viewport: { once: true, margin: "-80px" },
  transition: { duration: 0.6, ease: "easeOut" },
};

function ModeSwitch({ mode, setMode, label }) {
  const [pulse, setPulse] = useState(false);
  return (
    <Tooltip title={label}>
      <Button
        onClick={() => {
          setPulse(true);
          setTimeout(() => setPulse(false), 500);
          setMode((prev) => (prev === "dark" ? "light" : "dark"));
        }}
        sx={{
          position: "relative",
          backgroundColor: "#d4af37",
          color: mode === "dark" ? "#000" : "#222",
          fontWeight: "bold",
          borderRadius: 999,
          px: 2.5,
          "&:hover": { backgroundColor: "#bfa237" },
          overflow: "hidden",
        }}
      >
        {mode === "dark" ? "Light" : "Dark"}
        {pulse && (
          <motion.span
            initial={{ scale: 0, opacity: 0.5 }}
            animate={{ scale: 3, opacity: 0 }}
            transition={{ duration: 0.5, ease: "easeOut" }}
            style={{
              position: "absolute",
              inset: 0,
              borderRadius: "50%",
              background: "radial-gradient(rgba(255,255,255,.5), transparent)",
              pointerEvents: "none",
            }}
          />
        )}
      </Button>
    </Tooltip>
  );
}

function DroneCard({ drone, mode, gold }) {
  const { t } = useTranslation();
  const isNew = drone.name.toLowerCase().includes("new");
  const firstImage = droneImages[drone.id]?.[0] || null;
  const description = t(`drone_details.${drone.id}`, t("description_unavailable"));
  const shortDescription = description.length > 50 ? `${description.slice(0, 50)}...` : description;

  return (
    <motion.div
      initial={{ opacity: 0, y: 30 }}
      whileInView={{ opacity: 1, y: 0 }}
      viewport={{ once: true }}
      transition={{ duration: 0.5 }}
      style={{ position: "relative" }}
    >
      {isNew && (
        <motion.div
          initial={{ scale: 0 }}
          animate={{ scale: [1, 1.2, 1], rotate: [0, 15, -15, 0] }}
          transition={{ duration: 1.2, repeat: Infinity, repeatDelay: 2 }}
          style={{
            position: "absolute",
            top: 8,
            left: 8,
            backgroundColor: "#d4af37",
            color: "#000",
            fontWeight: "bold",
            padding: "2px 8px",
            borderRadius: 4,
            fontSize: 12,
            zIndex: 10,
            boxShadow: "0 0 10px rgba(212,175,55,0.6)",
          }}
        >
          NEW
        </motion.div>
      )}
      <Box
        component={Link}
        to={`/drones/${drone.id}`}
        sx={{
          display: "flex",
          alignItems: "center",
          textDecoration: "none",
          border: "1px solid rgba(255,215,55,0.25)",
          borderRadius: 3,
          p: 2,
          background: mode === "dark" ? "rgba(0,0,0,0.25)" : "rgba(255,255,255,0.35)",
          backdropFilter: "blur(10px) saturate(180%)",
          boxShadow: "0 8px 30px rgba(0,0,0,0.25)",
          transition: "transform .3s ease, box-shadow .3s ease",
          cursor: "pointer",
          "&:hover": {
            transform: "translateY(-6px) scale(1.03)",
            boxShadow: `0 0 30px ${gold ? "#d4af37" : "#6a0dad"}`,
          },
        }}
      >
        {firstImage && (
          <motion.img
            src={firstImage}
            alt={`${drone.name} preview`}
            style={{
              width: 80,
              height: 60,
              objectFit: "cover",
              borderRadius: 8,
              marginRight: 16,
              border: "1px solid rgba(255,215,55,0.25)",
            }}
            initial={{ opacity: 0, x: -10 }}
            whileInView={{ opacity: 1, x: 0 }}
            viewport={{ once: true }}
            transition={{ duration: 0.4 }}
          />
        )}
        <Box sx={{ flex: 1 }}>
          <Typography variant="h6" sx={{ fontWeight: 700, color: gold ? "#d4af37" : "#6a0dad" }}>
            {drone.name}
          </Typography>
          <Typography variant="body2" sx={{ opacity: 0.8, mt: 0.5 }}>
            {shortDescription}
          </Typography>
          <Typography variant="body2" sx={{ opacity: 0.8, color: gold ? "#d4af37" : "#6a0dad", mt: 0.5 }}>
            View details →
          </Typography>
        </Box>
      </Box>
    </motion.div>
  );
}

function Typewriter({ phrases, color = "#d4af37", interval = 120 }) {
  const [text, setText] = useState("");
  const [index, setIndex] = useState(0);
  const [subIndex, setSubIndex] = useState(0);
  const [forward, setForward] = useState(true);

  useEffect(() => {
    const timeout = setTimeout(() => {
      if (forward) {
        if (subIndex < phrases[index].length) setSubIndex(subIndex + 1);
        else setForward(false);
      } else {
        if (subIndex > 0) setSubIndex(subIndex - 1);
        else {
          setForward(true);
          setIndex((prev) => (prev + 1) % phrases.length);
        }
      }
      setText(phrases[index].substring(0, subIndex));
    }, interval);
    return () => clearTimeout(timeout);
  }, [subIndex, index, forward, phrases, interval]);

  return (
    <Typography
      variant="h5"
      sx={{ color, mt: 1, opacity: 0.9, fontWeight: 500, letterSpacing: 1.2 }}
    >
      {text}
      <span style={{ borderRight: `2px solid ${color}`, marginLeft: 2 }} />
    </Typography>
  );
}

