function DroneDetail({ mode }) {
  const { t } = useTranslation();
  const { droneId } = useParams();
  const allDrones = [...dronesList, ...sixthGenDrones, ...peacefulDrones];
  const drone = allDrones.find((d) => d.id === droneId);

  useEffect(() => {
    window.scrollTo({ top: 0, behavior: "smooth" });
  }, [droneId]);

  if (!drone) {
    return (
      <Container sx={{ mt: 6 }}>
        <Typography variant="h5">{t("drone_not_found")}</Typography>
        <Footer />
      </Container>
    );
  }

  const images = droneImages[droneId] || [];

  return (
    <Container sx={{ mt: 4 }}>
      <MotionBox {...fadeUp}>
        <Typography
          variant="h4"
          gutterBottom
          sx={{ fontWeight: 800, textAlign: "center", color: "#d4af37", textShadow: "0 0 10px rgba(212,175,55,.35)" }}
        >
          {drone.name}
        </Typography>
      </MotionBox>

      <Grid container spacing={2} justifyContent="center">
        {images.map((src, i) => (
          <Grid item key={i}>
            <motion.img
              src={src}
              alt={`${drone.name} ${i + 1}`}
              loading="lazy"
              style={{
                width: 240,
                height: 168,
                objectFit: "cover",
                borderRadius: 16,
                border: "1px solid rgba(255,215,55,0.25)",
                boxShadow: "0 8px 30px rgba(0,0,0,0.25)",
              }}
              initial={{ opacity: 0, y: 10 }}
              whileInView={{ opacity: 1, y: 0 }}
              viewport={{ once: true }}
              transition={{ duration: 0.4, delay: i * 0.05 }}
            />
          </Grid>
        ))}
      </Grid>

      <MotionBox
        {...fadeUp}
        sx={{
          mt: 3,
          border: "1px solid rgba(255,215,55,0.25)",
          borderRadius: 3,
          p: 3,
          background: mode === "dark" ? "rgba(0,0,0,0.25)" : "rgba(255,255,255,0.35)",
          backdropFilter: "blur(15px) saturate(180%)",
          boxShadow: "0 8px 30px rgba(0,0,0,0.25)",
        }}
      >
        <Typography variant="body1" sx={{ fontSize: 18, lineHeight: 1.7, whiteSpace: "pre-line", opacity: 0.95 }}>
          {t(`drone_details.${droneId}`, t("description_unavailable"))}
        </Typography>
      </MotionBox>

      <Footer />
    </Container>
  );
}

// ===============================
// اپلیکیشن اصلی
// ===============================
function App() {
  const { t } = useTranslation();
  const [mode, setMode] = useState("dark");

  const theme = useMemo(
    () =>
      createTheme({
        palette: {
          mode,
          primary: { main: "#d4af37" },
          secondary: { main: "#6a0dad" },
          background: { default: mode === "dark" ? "#0b0b0b" : "#f5f5f5", paper: mode === "dark" ? "#0b0b0b" : "#ffffff" },
          text: { primary: mode === "dark" ? "#d4af37" : "#222", secondary: mode === "dark" ? "#d4af37" : "#555" },
        },
        typography: { fontFamily: "Roboto, Arial, sans-serif" },
      }),
    [mode]
  );

  return (
    <ThemeProvider theme={theme}>
      <CssBaseline />
      <Router>
        <MeteorBackground mode={mode} />
        <AppBar position="fixed" color="transparent" elevation={0}>
          <Toolbar sx={{ justifyContent: "flex-end", gap: 2 }}>
            <ModeSwitch mode={mode} setMode={setMode} label="Toggle Dark/Light Mode" />
          </Toolbar>
        </AppBar>

        <Box sx={{ mt: 8 }}>
          <Routes>
            <Route path="/" element={<Home mode={mode} />} />
            <Route path="/drones" element={<DronesPage mode={mode} />} />
            <Route path="/sixth-gen-drones" element={<SixthGenDronesPage mode={mode} />} />
            <Route path="/peaceful-drones" element={<PeacefulDronesPage mode={mode} />} />
            <Route path="/drones/:droneId" element={<DroneDetail mode={mode} />} />
            <Route path="*" element={<Navigate to="/" replace />} />
          </Routes>
        </Box>
      </Router>
    </ThemeProvider>
  );
}

export default App;