import React, { useEffect, useRef } from "react";

export default function MeteorBackground({ mode }) {
  const canvasRef = useRef();

  useEffect(() => {
    const canvas = canvasRef.current;
    const ctx = canvas.getContext("2d");
    let width = (canvas.width = window.innerWidth);
    let height = (canvas.height = window.innerHeight);

    // تعریف ستاره‌های چشمک‌زن
    const stars = Array.from({ length: 200 }, () => ({
      x: Math.random() * width,
      y: Math.random() * height,
      radius: Math.random() * 1.5 + 0.5,
      opacity: Math.random() * 0.5 + 0.2,
      twinkleSpeed: Math.random() * 0.02 + 0.01,
    }));

    // تابع رسم ستاره‌ها
    function drawStars() {
      stars.forEach((star) => {
        ctx.beginPath();
        ctx.arc(star.x, star.y, star.radius, 0, Math.PI * 2);
        ctx.fillStyle = mode === "dark" 
          ? `rgba(255, 255, 255, ${star.opacity})` 
          : `rgba(180, 150, 0, ${star.opacity})`; // طلایی برای ستاره‌ها در لایت مود
        ctx.fill();
        star.opacity += star.twinkleSpeed * (Math.random() > 0.5 ? 1 : -1);
        if (star.opacity > 0.7) star.opacity = 0.7;
        if (star.opacity < 0.2) star.opacity = 0.2;
      });
    }

    // تابع انیمیشن
    function animate() {
      // رسم گرادیان پس‌زمینه
      const gradient = ctx.createLinearGradient(0, 0, 0, height);
      if (mode === "dark") {
        gradient.addColorStop(0, "rgba(10, 10, 30, 0.95)");
        gradient.addColorStop(1, "rgba(0, 0, 20, 0.95)");
      } else {
        gradient.addColorStop(0, "rgba(4, 59, 8, 0.95)"); // سبز ملایم
        gradient.addColorStop(1, "rgba(0, 0, 0, 0.95)"); // سفید سلطنتی
      }
      ctx.fillStyle = gradient;
      ctx.fillRect(0, 0, width, height);

      // رسم ستاره‌ها
      drawStars();

      requestAnimationFrame(animate);
    }

    animate();

    // مدیریت تغییر اندازه پنجره
    const handleResize = () => {
      width = canvas.width = window.innerWidth;
      height = canvas.height = window.innerHeight;
      stars.forEach((star) => {
        star.x = Math.random() * width;
        star.y = Math.random() * height;
      });
    };
    window.addEventListener("resize", handleResize);
    return () => window.removeEventListener("resize", handleResize);
  }, [mode]);

  return (
    <canvas
      ref={canvasRef}
      style={{
        position: "fixed",
        top: 0,
        left: 0,
        width: "100%",
        height: "100%",
        zIndex: -6,
        pointerEvents: "none",
      }}
    />
  );
}